/* -------------Contact page css-------- */

.contacts .container form {
  text-align: center;
  max-width: 550px;
  margin: 0 auto;
  padding: 20px 40px;

  border: 1px solid rgba(0, 0, 0, 0.1);
}

.contacts .container form input {
  width: 100%;
  height: 40px;
  margin: 15px auto;
  display: block;
  border: 0px;
  border-bottom: 2px solid #262626;
  transition: 0.4s all ease;
  padding-left: 4px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 16px;
  outline: none;
}

.contacts .container form input:hover {
  padding-left: 10px;
}

.contacts .container form textarea {
  margin: 20px 0px;
  width: 100%;
}

.contacts .container form button:hover {
  background-color: #252525;
  color: #f5f5f5;
}

.contacts .container form .visible-icon {
  cursor: pointer;
}

/* --------------Footer page css------------- */

.footer p {
  padding: 15px 0px;
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
}

.cubeBox {
  -webkit-perspective: 800px;
          perspective: 800px;
  margin: 0 auto;
  -webkit-perspective-origin: 50% 100px;
          perspective-origin: 50% 100px;
}

.cube {
  position: relative;
  width: 200px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  animation: spin 10s infinite alternate-reverse linear;
}

.cube div {
  position: absolute;
  text-align: center;
  width: 200px;
  height: 200px;
  top: -80px;
}

.back {
  -webkit-transform: translateZ(-100px) rotateY(180deg);
          transform: translateZ(-100px) rotateY(180deg);
  background: url(/React-procejt1/static/media/html.6671754e.jpg) no-repeat;
  background-position: center center;
  background-size: cover;
  -moz-box-reflect: below 0px
    linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
  -webkit-box-reflect: below 0px
    linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
}
.right {
  -webkit-transform: rotateY(-270deg) translateX(100px);
          transform: rotateY(-270deg) translateX(100px);
  -webkit-transform-origin: top right;
          transform-origin: top right;
  background: url(/React-procejt1/static/media/css.6ac464f3.png) no-repeat;
  background-position: center center;
  background-size: cover;
  -moz-box-reflect: below 0px
    linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
  -webkit-box-reflect: below 0px
    linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
}
.left {
  -webkit-transform: rotateY(270deg) translateX(-100px);
          transform: rotateY(270deg) translateX(-100px);
  -webkit-transform-origin: center left;
          transform-origin: center left;
  background: url(/React-procejt1/static/media/js.b7cef96c.jpg) no-repeat;
  background-position: center center;
  background-size: cover;
  -moz-box-reflect: below 0px
    linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
  -webkit-box-reflect: below 0px
    linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
}
.top {
  -webkit-transform: rotateX(-90deg) translateY(-100px);
          transform: rotateX(-90deg) translateY(-100px);
  -webkit-transform-origin: top center;
          transform-origin: top center;
  background: url(/React-procejt1/static/media/bootstrap.6df19992.png) no-repeat;
  background-position: center center;
  background-size: cover;
}
.bottom {
  -webkit-transform: rotateX(90deg) translateY(100px);
          transform: rotateX(90deg) translateY(100px);
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
  background: url(/React-procejt1/static/media/react.da790228.jpg) no-repeat;
  background-position: center center;
  background-size: cover;
}
.front {
  -webkit-transform: translateZ(100px);
          transform: translateZ(100px);
  background: url(/React-procejt1/static/media/jquery.68479d86.jpg) no-repeat;
  background-position: center center;
  background-size: cover;
  -moz-box-reflect: below 0px
    linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
  -webkit-box-reflect: below 0px
    linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotateY(0deg) rotateX(60deg);
            transform: rotateY(0deg) rotateX(60deg);
  }

  50% {
    -webkit-transform: rotateY(360deg) rotateX(-60deg);
            transform: rotateY(360deg) rotateX(-60deg);
  }

  100% {
    -webkit-transform: rotateY(0deg) rotateX(0deg);
            transform: rotateY(0deg) rotateX(0deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotateY(0deg) rotateX(60deg);
            transform: rotateY(0deg) rotateX(60deg);
  }

  50% {
    -webkit-transform: rotateY(360deg) rotateX(-60deg);
            transform: rotateY(360deg) rotateX(-60deg);
  }

  100% {
    -webkit-transform: rotateY(0deg) rotateX(0deg);
            transform: rotateY(0deg) rotateX(0deg);
  }
}

@media (max-width: 990px) {
  .cube div {
    height: 150px !important;
    width: 150px !important;
  }

  .back {
    -webkit-transform: translateZ(-50px) rotateY(180deg);
            transform: translateZ(-50px) rotateY(180deg);
  }
  .right {
    -webkit-transform: rotateY(-270deg) translateX(50px);
            transform: rotateY(-270deg) translateX(50px);
  }
  .left {
    -webkit-transform: rotateY(270deg) translateX(-50px);
            transform: rotateY(270deg) translateX(-50px);
  }
  .top {
    -webkit-transform: rotateX(-90deg) translateY(-100px);
            transform: rotateX(-90deg) translateY(-100px);
  }
  .bottom {
    -webkit-transform: rotateX(90deg) translateY(100px);
            transform: rotateX(90deg) translateY(100px);
  }
  .front {
    -webkit-transform: translateZ(100px);
            transform: translateZ(100px);
  }
}

@media (max-width: 775px) {
  .cube div {
    top: 30px;
    left: 0px;
  }
}

button.navbar-toggler {
  background-color: #e5e8e8 !important;
}

.menuSection {
  position: relative;
  background-color: #000;
}

.myMenu {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  z-index: 9;
}

.myMenu nav {
  background: transparent !important;
}

.myMenu nav .navbar-brand {
  font-family: "Italianno", cursive;
  font-size: 35px;
  text-transform: capitalize;
  color: #ffffff !important;
}

.myMenu nav .navbar-brand:hover {
  color: #edff4a !important;
}

nav ul.ml-auto {
  margin-left: auto !important;
}

nav ul li {
  margin: 10px;
}

nav ul li a {
  color: #ffffff !important;
  padding: 4px 0px !important;
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  text-transform: capitalize;
  letter-spacing: 0.5px;
}

nav ul li .active {
  color: rgb(237, 255, 74) !important;
  border-bottom: 2px solid #ffffff;
}

@media (max-width: 995px) {
  nav ul li .active {
    border-bottom: 0px;
  }
}

